<template><section><html><head></head><body><h1>BillboardGraphics</h1>
<p>The <code v-pre="">vc-graphics-billboard</code> component is used to load billboard (a two dimensional icon) and needs to be used as a subcomponent of <code v-pre="">vc-entity</code>.</p>
<h2>Example</h2>
<h3>Load a BillboardGraphics with vc-graphics-billboard</h3>
<h4>Preview</h4>
<doc-preview>
 <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-entity ref="entity" :position="position" :description="description" :id="id" :billboard.sync="billboard">
           <vc-graphics-billboard :image="image" :scale="0.1" :show="show" :distance-display-condition="distanceDisplayCondition" :horizontal-origin="horizontalOrigin"></vc-graphics-billboard>
        </vc-entity>
      </vc-viewer>
      <div class="demo-tool">
        <span>{{ show ? 'Hide' : 'Show' }}</span>
        <md-switch v-model="show"></md-switch>
      </div>
    </div>
  </template>
  
</doc-preview>
