<h2>Instance Properties</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>name</th>
<th>type</th>
<th>default</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>show</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code> A boolean Property specifying the visibility of the billboard.</td>
</tr>
<tr>
<td>image</td>
<td>String|Object</td>
<td></td>
<td><code v-pre="">optional</code> A Property specifying the Image, URI, or Canvas to use for the billboard.</td>
</tr>
<tr>
<td>scale</td>
<td>Number</td>
<td><code v-pre="">1.0</code></td>
<td><code v-pre="">optional</code> A numeric Property specifying the scale to apply to the image size.</td>
</tr>
<tr>
<td>pixelOffset</td>
<td>Object</td>
<td><code v-pre="">{x: 0, y: 0}</code></td>
<td><code v-pre="">optional</code> A Cartesian2 Property specifying the pixel offset. <strong>structure: { x: number, y: number }</strong></td>
</tr>
<tr>
<td>eyeOffset</td>
<td>Object</td>
<td><code v-pre="">{x: 0, y: 0, z: 0}</code></td>
<td><code v-pre="">optional</code> A Cartesian3 Property specifying the eye offset.</td>
</tr>
<tr>
<td>horizontalOrigin</td>
<td>Number</td>
<td><code v-pre="">0</code></td>
<td><code v-pre="">optional</code> A Property specifying the HorizontalOrigin. <strong>CENTER: 0, LEFT: 1, RIGHT: -1</strong></td>
</tr>
<tr>
<td>verticalOrigin</td>
<td>Number</td>
<td><code v-pre="">0</code></td>
<td><code v-pre="">optional</code> A Property specifying the VerticalOrigin. <strong>CENTER: 0, BOTTOM: 1, BASELINE: 2, TOP: -1</strong></td>
</tr>
<tr>
<td>heightReference</td>
<td>Number</td>
<td><code v-pre="">0</code></td>
<td><code v-pre="">optional</code> A Property specifying what the height is relative to. <strong>NONE: 0, CLAMP_TO_GROUND: 1, RELATIVE_TO_GROUND: 2</strong></td>
</tr>
<tr>
<td>color</td>
<td>Object|String</td>
<td><code v-pre="">'white'</code></td>
<td><code v-pre="">optional</code> A Property specifying the tint Color of the image.</td>
</tr>
<tr>
<td>rotation</td>
<td>Number</td>
<td><code v-pre="">0</code></td>
<td><code v-pre="">optional</code> A numeric Property specifying the rotation about the alignedAxis. <strong>structure: { x: number, y: number, z: number }</strong></td>
</tr>
<tr>
<td>alignedAxis</td>
<td>Object</td>
<td><code v-pre="">{x: 0, y: 0, z: 0}</code></td>
<td><code v-pre="">optional</code> A Cartesian3 Property specifying the unit vector axis of rotation.</td>
</tr>
<tr>
<td>sizeInMeters</td>
<td>Boolean</td>
<td></td>
<td><code v-pre="">optional</code> A boolean Property specifying whether this billboard's size should be measured in meters.</td>
</tr>
<tr>
<td>width</td>
<td>Number</td>
<td></td>
<td><code v-pre="">optional</code> A numeric Property specifying the width of the billboard in pixels, overriding the native size.</td>
</tr>
<tr>
<td>height</td>
<td>Number</td>
<td></td>
<td><code v-pre="">optional</code> A numeric Property specifying the height of the billboard in pixels, overriding the native size.</td>
</tr>
<tr>
<td>scaleByDistance</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> A NearFarScalar Property used to scale the point based on distance from the camera. <strong>structure: { near: number, nearValue: number, far: number, farValue: number }</strong></td>
</tr>
<tr>
<td>translucencyByDistance</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> A NearFarScalar Property used to set translucency based on distance from the camera. <strong>structure: { near: number, nearValue: number, far: number, farValue: number }</strong></td>
</tr>
<tr>
<td>pixelOffsetScaleByDistance</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> A NearFarScalar Property used to set pixelOffset based on distance from the camera. <strong>structure: { near: number, nearValue: number, far: number, farValue: number }</strong></td>
</tr>
<tr>
<td>imageSubRegion</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> A Property specifying a BoundingRectangle that defines a sub-region of the image to use for the billboard, rather than the entire image, measured in pixels from the bottom-left.</td>
</tr>
<tr>
<td>distanceDisplayCondition</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> A Property specifying at what distance from the camera that this billboard will be displayed. <strong>structure: { near: number, far: number }</strong></td>
</tr>
<tr>
<td>disableDepthTestDistance</td>
<td>Number</td>
<td></td>
<td><code v-pre="">optional</code> A Property specifying the distance from the camera at which to disable the depth test to.</td>
</tr>
</tbody>
</table>
<hr>
<ul>
<li>Refer to the official document: <strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/BillboardGraphics.html">BillboardGraphics</a></strong></li>
</ul>
<h2>Events</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>name</th>
<th>parameter</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>Triggers when the component is ready. It returns a core class of Cesium, a viewer instance, and the cesiumObject.</td>
</tr>
<tr>
<td>definitionChanged</td>
<td></td>
<td>Triggers whenever a property or sub-property is changed or modified.</td>
</tr>
</tbody>
</table>
<hr>
</body></html></section></template>

<script>
    export default {
      data () {
        return {
          id: 'Hello Vue Cesium',
          description: 'This is a billboard',
          image: 'https://zouyaoji.top/vue-cesium/favicon.png',
          position: { lng: 90, lat: 40, height: 10000 },
          billboard: {},
          show: true,
          distanceDisplayCondition: { near: 0, far: 20000000 },
          horizontalOrigin: 0
        }
      },
      mounted () {
        this.$refs.entity.createPromise.then( ({Cesium, viewer, cesiumObject}) => {
          viewer.zoomTo(cesiumObject)
        })
      },
      methods: {
        ready (cesiumInstance) {
          const {Cesium, viewer} = cesiumInstance
        }
      }
    }
  </script>